<template>
  <div :class="`${prefixCls}`">
    <head-top :title='`${_$type.label}`' :append='true' :go-back='true'>
      <div 
      slot='append' 
      class="c_59b6d7" 
      @click='changeType'
      >{{ _$type.label2 }}</div>
    </head-top>
    <div :class="`${prefixCls}--main`">
      <div class="signup-form--row">
        <label class="signup-form--row-prepend" for="username"></label>
        <input id="username" v-model.trim='name' type="text" placeholder="2-8" maxlength="8">
        <span class="signup-form--row-append">
          <svg v-show='name.length > 0' @click='name = ""'>
            <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#base-clean"></use>
          </svg>
        </span>
      </div>

      <div class="signup-form--row" v-if='verifiable_type === "sms"'>
        <label class="signup-form--row-prepend" for="phone"></label>
        <input id="phone" v-model.trim.number='phone' type="number" maxlength="11" placeholder="11" oninput="if(value.length>11)value=value.slice(0,11)">
        <span 
        class="signup-form--row-append c_59b6d7" 
        :class='{ disabled: phone.length < 11 }'
        @click='getCode'
        >{{ codeText }}</span>
      </div>

      <div class="signup-form--row" v-if='verifiable_type === "mail"'>
        <label class="signup-form--row-prepend" for="mail"></label>
        <input id="mail" type="email" v-model.trim='email' placeholder="">
        <span 
        class="signup-form--row-append c_59b6d7" 
        :class='{ disabled: email.length < 4 }'
        @click='getCode'
        >{{ codeText }}</span>
      </div>

      <div class="signup-form--row">
        <label class="signup-form--row-prepend" for="code"></label>
        <input id="code" type="text" v-model.trim='verifiable_code' placeholder="4-6" maxlength="6">
        <span class="signup-form--row-append">
          <svg v-show='verifiable_code.length > 0' @click='verifiable_code = ""'>
            <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#base-clean"></use>
          </svg>
        </span>
      </div>

      <div class="signup-form--row">
        <label class="signup-form--row-prepend" for="psw"></label>
        <input id="psw" v-if='eye' type="text" v-model.trim='password' placeholder="6">
        <input id="psw" v-else type="password" v-model.trim='password' placeholder="6">
        <span class="signup-form--row-append">
          <svg @click='eye = !eye'>
            <use xmlns:xlink="http://www.w3.org/1999/xlink" :xlink:href="`#eye-${eye ? 'open' : 'close'}`"></use>
          </svg>
        </span>
      </div>
      <!-- {{ error | plusMessageAnalyze }} -->
      <div class="signup-form--error ellipsis"></div>
    </div>
      <button class="signup-form--submit-btn" :disabled='disabled' @click='signIn'>
        <span></span>
        <svg v-if='loading' class="rotate">
          <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#base-loading"></use>
        </svg>
      </button>
  </div>
</template>
<script>
function strLength(str) {
  let totalLength = 0;
  let i = 0;
  let charCode;
  for (; i < str.length; i++) {
    charCode = str.charCodeAt(i);
    if (charCode < 0x007f) {
      totalLength = totalLength + 1;
    } else if (charCode >= 0x0080 && charCode <= 0x07ff) {
      totalLength += 2;
    } else if (charCode >= 0x0800 && charCode <= 0xffff) {
      totalLength += 3;
    }
  }
  return totalLength;
}
const prefixCls = "signup";
const SMS = "sms"; // 
const EMAIL = "mail"; // 

// 
const phoneReg = /^(((13[0-9]{1})|14[0-9]{1}|(15[0-9]{1})|17[0-9]{1}|(18[0-9]{1}))+\d{8})$/;
// 
const emailReg = /^\w+((-\w+)|(\.\w+))*@[A-Za-z0-9]+((\.|-)[A-Za-z0-9]+)*\.[A-Za-z0-9]+$/;
// 
const usernameReg = /^[a-zA-Z_\u4E00-\u9FA5\uF900-\uFA2D][a-zA-Z0-9_\u4E00-\u9FA5\uF900-\uFA2D]*$/;
// 
// const codeReg = /^[0-9]{4,6}$/;

export default {
  name: "signup",
  data() {
    return {
      prefixCls,
      eye: false,
      error: "",
      loading: false,

      name: "",
      email: "",
      phone: "",
      countdown: 0,
      password: "",
      verifiable_type: SMS,
      verifiable_code: ""
    };
  },
  computed: {
    codeText() {
      return this.countdown > 0 ? `${this.countdown}` : "";
    },
    canGetCode() {
      return (
        (this.phone.length === 11 || this.email.length > 4) &&
        this.countdown === 0
      );
    },
    disabled() {
      const {
        name,
        phone,
        email,
        password,
        verifiable_code: verifiableCode,
        verifiable_type: verifiableType
      } = this.$data;

      const res = [name, password, verifiableCode, verifiableType].every(
        i => i !== ""
      );

      return !(res && (phone.length === 11 || email.length > 4));
    },
    _$type: {
      get() {
        let label = "";
        let label2 = "";
        switch (this.verifiable_type) {
          case SMS:
            label = "";
            label2 = "";
            break;
          case EMAIL:
            label = "";
            label2 = "";
            break;
        }
        return {
          value: this.verifiable_type,
          label,
          label2
        };
      },
      set(val) {
        this.verifiable_type = val;
      }
    }
  },
  methods: {
    countDown() {
      const t = setInterval(() => {
        if (--this.countdown <= 0) {
          this.countdown = 0;
          clearInterval(t);
        }
      }, 1000);
    },
    getCode() {
      if (!this.canGetCode) return;
      const phone = this.phone;
      const email = this.email;
      let param = {
        phone,
        email
      };
      this.verifiable_type === SMS ? delete param.email : delete param.phone;
      this.$http
        .post("verifycodes/register", param, {
          validateStatus: status => status === 202
        })
        .then(() => {
          this.countdown = 60;
          this.countDown();
          this.$Message.success("");
          this.error = "";
        })
        .catch(
          ({
            response: { status = null, data: { errors = {} } = {} } = {}
          }) => {
            if (status === 500) {
              this.error = { message: "," };
              return;
            }
            if (status === 422) {
              this.error = errors;
            }
            setTimeout(() => {
              this.error = "";
            }, 3000);
          }
        );
    },
    signIn() {
      const {
        name,
        phone,
        email,
        password,
        verifiable_code: verifiableCode,
        verifiable_type: verifiableType
      } = this.$data;

      // 
      if (!usernameReg.test(name)) {
        this.$Message.error({ name: "" });
        return;
      }

      // 
      if (!isNaN(name[0])) {
        this.$Message.error({ name: "" });
        return;
      }

      // 
      if (strLength(name) > 48 || strLength(name) < 4) {
        this.$Message.error({ name: "24" });
        return;
      }

      // 
      if (verifiableType === SMS && !phoneReg.test(phone)) {
        this.$Message.error({ phone: "" });
        return;
      }

      // 
      if (verifiableType !== SMS && !emailReg.test(email)) {
        this.$Message.error({ email: "" });
        return;
      }

      // 
      if (password.length < 6) {
        this.$Message.error({ password: "6" });
        return;
      }

      let param = {
        name,
        phone,
        email,
        verifiable_code: verifiableCode,
        password,
        verifiable_type: verifiableType,
        validateStatus: s => s === 201
      };
      this.loading = true;
      verifiableType === SMS ? delete param.email : delete param.phone;
      this.$http
        .post("users", param)
        .then(({ data: { token } = {} }) => {
          if (token) {
            this.loading = false;
            this.$Message.success(", ");
            this.$router.push("/signin");
          }
        })
        .catch(({ response: { data: { errors = {} } = {} } = {} }) => {
          this.$Message.error(errors);
        });
    },
    changeType() {
      switch (this.verifiable_type) {
        case SMS:
          this._$type = EMAIL;
          break;
        case EMAIL:
          this._$type = SMS;
          break;
      }
    }
  }
};
</script>
<style lang='less' src='./style/signup.less'>
</style>



